$font: "Gilroy Extra Bold";
$proximasemi: "Proxima Nova Semibold";
$proxima: "Proxima Nova";
$whiteColor: #ffffff;

.header__section_body {
  height: 768px;
  background-image: url(../../assets/header/bg.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.header__section-line {
  width: 100%;
  border-bottom: 0;
  height: 0px;
  color: $whiteColor;
  opacity: 0.2;
  border: 1px solid $whiteColor;
}

.header__section-maintext {
  font-family: $font;
  font-size: 42px;
  font-weight: bold;
  line-height: 51px;
  color: $whiteColor;
  white-space: pre-line;
  letter-spacing: -0.5px;
}

.header__section-secondarytext {
  font-size: 20px;
  font-family: $proximasemi;
  line-height: 34px;
  color: $whiteColor;
  opacity: 0.7;
}

.header__section-button {
  width: 340px;
  height: 70px;
  font-family: $font;
  font-weight: 800;
  font-size: 12px;
  background-color: #fee040;
  border: none;
  background-image: url(../../assets/header/download.png);
  background-size: 12px 12px;
  background-repeat: no-repeat;
  background-position: center left 58px;
  text-indent: 12px;
  letter-spacing: 2px;
}

.header__section-texts {
  display: flex;
  gap: 235px;
  justify-content: center;
}

.header__section-left-section {
  display: flex;
  margin-top: 120px;
  gap: 38px;
  flex-direction: column;
}

.header__section-right-section {
  display: flex;
  margin-top: 132px;
  gap: 40px;
  flex-direction: column;
}

.header__section-send-task {
  font-size: 15px;
  line-height: 28px;
  color: $whiteColor;
}

.header__section-best-qualities {
  display: flex;
  align-items: flex-end;
  flex-direction: row;
  gap: 80px;
}

.header__section-stamp {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 210px;
  width: 210px;
  background-image: url(../../assets/header/stamp.png);
  background-repeat: no-repeat;
  background-size: 210px;
  background-position: center;
}

.header__section-man {
  display: flex;
  height: 120px;
  border-radius: 100px;
}

.header__sections {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 66px;
}

.header__section-bottom {
  display: flex;
  gap: 180px;
  justify-content: end;
  margin-right: 230px;
}

.header__section-number-quality {
  font-family: $proxima;
  margin: 0;
  font-size: 38px;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px #f2c94c;
}

.header__section-quaality-part {
  display: flex;
  gap: 30px;
}

.header__section-qualities {
  display: flex;
}
.header__section-send-task {
  font-family: $proxima;
}

.header__section-text-quality {
  font-family: $proxima;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: -0.5px;
  color: $whiteColor;
}

// max-width: 1200px
@media screen and (max-width: 1200px) {
  .header__section {
    gap: 10.5%;
  }

  .header__section_body {
    height: 1000px;
  }

  .header__menu-left {
    gap: 60px;
  }

  .header__menu-right {
    gap: 20px;
  }
  .header__section-texts {
    display: flex;
    gap: 20px;
    flex-direction: column;
    justify-content: center;
  }

  .header__section-left-section {
    display: flex;
    align-items: center;
  }

  .header__section-maintext {
    text-align: center;
  }

  .header__section-secondarytext {
    text-align: center;
  }

  .header__section-right-section {
    margin-top: 0px;
    gap: 20px;
    align-items: center;
  }

  .header_section-audio {
    margin-top: auto;
  }

  .header__section-bottom {
    gap: 180px;
    margin-right: 60px;
  }

  .header__section-best-qualities {
    display: flex;
    flex-direction: column;
    gap: 37px;
    align-items: flex-start;
  }

  .header__section-number-quality {
    width: 40px;
  }
}
