$bebas: "Bebas Neue Regular";
$gilroy: "Gilroy";
$proxima: "Proxima Nova";
$proximaRegular: "Proxima Nova Regular";

.complex__section-body {
  display: flex;
  flex-direction: column;
  gap: 36px;
  height: 881px;
}

.complex__section-title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 104px;
  font-family: $bebas;
  font-weight: bold;
  font-size: 80px;
  line-height: 64px;
  letter-spacing: 5px;
  background: linear-gradient(180deg, #60b6ff 0%, rgba(0, 78, 134, 0.8) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.complex__section-description {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: $gilroy;
  font-weight: bold;
  font-size: 20px;
  line-height: 28px;
  color: #505050;
}

.complex__image {
  width: 960px;
}

.complex__section-works {
  display: flex;
}

.complex__section-preparatory-works {
  background-color: #f5f5f5;
  width: 100%;
}

.complex__section-step {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fee040;
  height: 27px;
  width: 107px;
}

.complex__section-text {
  font-family: $proxima;
  font-weight: bold;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 3px;
  color: #151515;
}

.complex__section-step-text {
  font-family: $proxima;
  font-weight: bold;
  font-size: 24px;
  line-height: 34px;
  letter-spacing: -0.5px;
  color: #151515;
  margin-top: -3px;
}

.complex__section-head {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin-left: 70px;
  margin-top: 80px;
}

.complex__section-work-text {
  font-family: $proxima;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: #333333;
  opacity: 0.7;
}

.complex__section-author-work {
  display: flex;
  margin-top: 18px;
  margin-left: 70px;
  flex-direction: column;
  gap: 32px;
}

.complex__section-author-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  width: 70px;
  background: #ffffff;
  box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.2);
  border-radius: 50px;
}

.complex_section-iamge-author {
  display: flex;
  height: 60px;
  border-radius: 50px;
  width: 60px;
}

.complex__section-table {
  display: flex;
  gap: 30px;
}

.complex__section-author-text {
  font-family: $proximaRegular;
  font-style: italic;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.5px;
  color: #505050;
  opacity: 0.7;
}

.complex__section-author-information {
  font-family: $proxima;
  font-weight: 700;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: #151515;
  opacity: 0.7;
  margin-top: 21px;
  margin-left: 170px;
}

//max-width 1200px
@media screen and (max-width: 1200px) {
  .complex__section-title {
    text-align: center;
  }

  .complex__section-works {
    display: flex;
    flex-direction: column;
  }

  .complex__section-table {
    justify-content: center;
  }

  .complex__section-author-information {
    text-align: center;
    margin-left: 140px;
  }

  .complex__section-work-text {
    text-align: center;
  }

  .complex__section-author-work {
    margin-left: 0px;
    justify-content: center;
    flex-direction: column;
    gap: 32px;
  }

  .complex__section-head {
    flex-direction: row;
    justify-content: center;
    gap: 20px;
    margin-left: 0px;
    margin-top: 80px;
  }
}
