$bebas: "Bebas Neue Regular";
$gilroy: "Gilroy";
$proxima: "Proxima Nova";

    .services__section-body{
        display: flex;
        flex-direction: column;
        gap: 20px;
        height: 753px;
        background-image: url(../../assets/services/services-background.png);
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }

    .services__section-title{
        display: flex;
        margin-top: 100px;
        justify-content: center;
        font-family: $bebas;
        font-weight: 700;
        font-size: 20px;
        line-height: 16px;
        letter-spacing: 20px;
        color: #FEE040;
    }

    .services__section-description{
        display: flex;
        justify-content: center;
        font-family: $gilroy;
        font-size: 32px;
        font-weight: 700;
        line-height: 42px;
        letter-spacing: -0.5px;
        color: #FFFFFF;
        word-wrap: normal;
    }

    .services__section-slider-container{
        margin-top: 55px;
        margin-left: 250px;
    }

    .services__section-slider-name{
        margin-top: 15px;
        width: 400px;
        font-family: $proxima;
        font-weight: 800;
        font-size: 20px;
        line-height: 28px;
        letter-spacing: -0.5px;
        color: #FFFFFF;
    }

    .services__section-slider-description{
        margin-top: 20px;
        height: 78px;
        font-family: "Proxima Nova Semibold";
        width: 330px;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: -0.5px;
        color: #FFFFFF;
        opacity: 0.7;
        word-break:normal;
    }

    .services__section-slider-more{
        margin-top: 29px;
        font-family: $bebas;
        font-size: 20px;
        line-height: 16px;
        letter-spacing: 7px;
        color: #FEE040;
    }

    .rec-dot, rec-dot_active{
        display: none;
    }

    .services__section-slider-img{
        width: 400px;
    }
// max-width: 1200px
@media screen and (max-width: 1200px){
    .services__section-description {
        text-align: center;
    }
}