$gilroy: "Gilroy";
$proxima: "Proxima Nova";

.description__section-body {
  display: flex;
  height: 449px;
}

.description__section-our-reports {
  display: flex;
  flex-direction: column;
  height: 449px;
  width: 850px;
  gap: 50px;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
}

.description__section-main-text {
  font-family: $gilroy;
  font-weight: 800;
  font-size: 32px;
  line-height: 42px;
  letter-spacing: -0.5px;
}

.description__section-secondary-text {
  font-family: $proxima;
  font-weight: 600;
  size: 18px;
  margin-right: 20px;
  color: #707070;
  line-height: 28px;
  letter-spacing: -0.5px;
}

.description__section-you-receive {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 960px;
  height: 449px;
  background: #f5f5f5;
}

.description__section-text {
  font-family: $proxima;
  font-weight: 700;
  font-size: 24px;
  line-height: 34px;
  letter-spacing: -0.5px;
}

.description__section-list {
  list-style-type: none;
  padding: 0;
  font-family: $proxima;
  font-weight: 600;
  size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: #333333;
  opacity: 0.7;
}

.description__section-list li {
  padding: 3px 0 0 50px;
  background-image: url(../../assets/description/tick.png);
  background-repeat: no-repeat;
  background-size: 30px 30px;
  height: 42px;
}

.description__section-img {
  position: absolute;
  width: 300px;
  height: 295px;
}

.description__section-div-img {
  display: flex;
  justify-content: center;
  align-items: center;
}

// max-width: 1200px
@media screen and (max-width: 1200px) {
  .description__section-our-reports {
    width: 100%;
    height: 50%;
  }

  .description__section-you-receive {
    width: 100%;
    height: 50%;
  }

  .description__section-img {
    width: 250px;
    height: 245px;
  }

  .description__section-body {
    flex-direction: column;
    height: 1100px;
  }
}
