$font: "Proxima Nova";
$semibold: "Proxima Nova Bold";
$whiteColor: #ffffff;

.header__section {
  width: 100%;
  display: flex;
  gap: 31.5%;
  justify-content: center;
  align-items: center;
  height: 80px;
}

.header__menu-left {
  display: flex;
  gap: 80px;
  align-items: center;
}

.header__menu-right {
  display: flex;
  gap: 90px;
  align-items: center;
}

.header__menu {
  height: 22px;
  width: 40px;
}

.header__logo {
  height: 30px;
}

.header__mail-email {
  height: 7.5px;
  width: 10px;
}

.header__phone {
  opacity: 0.5;
  height: 11px;
  width: 11px;
}

.header__mail {
  display: flex;
  font-size: 12px;
  gap: 6px;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  color: $whiteColor;
  font-family: $font;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: -0.5px;
}

.header__mail p {
  margin: 0;
}
.header__contacts {
  display: flex;
  gap: 7px;
  flex-direction: column;
  height: 100%;
  font-family: $font;
  font-weight: 700;
  justify-content: center;
  text-align: right;
  font-size: 20px;
}
.header__contacts p {
  margin: 0;
  color: $whiteColor;
}
.header__contacts-container {
  display: flex;
  gap: 7px;
  align-items: center;
  color: $whiteColor;
  font-family: $font;
  font-weight: 600;
  justify-content: end;
}

.header__contacts-text {
  margin: 0;
  opacity: 0.5;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: -0.5px;
  font-weight: 400;
}

.header__mail-container {
  align-items: center;
  display: flex;
  gap: 9.5px;
}

.header__mail-container p {
  margin: 0;
  color: #fee040;
  font-size: 14px;
}

.header__mail-write {
  opacity: 0.5;
  font-weight: 400;
  letter-spacing: -0.5px;
  line-height: 12px;
}

// max-width: 713px
@media screen and (max-width: 713px) {
  .header__section {
    gap: 5%;
  }
}
