body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Proxima Nova";
  src: url("./assets/fonts/ProximaNova-Regular.woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Proxima Nova";
  src: url("./assets/fonts/ProximaNova-Bold.woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Proxima Nova";
  src: url("./assets/fonts/ProximaNova-Extrabld.woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Proxima Nova";
  src: url("./assets/fonts/ProximaNova-Semibold.woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("./assets/fonts/Gilroy-ExtraBold.woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Bebas Neue Regular';
  src: url('./assets/fonts/BebasNeueRegular.eot');
  src: url('./assets/fonts/BebasNeueRegular.eot?#iefix') format('embedded-opentype'),
  url('./assets/fonts/BebasNeueRegular.woff') format('woff'),
  url('./assets/fonts/BebasNeueRegular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  }

  @font-face {
    font-family: 'Proxima Nova Semibold';
    src: url('./assets/fonts/ProximaNova-Semibold.eot');
    src: url('./assets/fonts/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
    url('./assets/fonts/ProximaNova-Semibold.woff') format('woff'),
    url('./assets/fonts/ProximaNova-Semibold.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    }

    @font-face {
      font-family: 'Proxima Nova Regular';
      src: url('./assets/fonts/ProximaNova-Regular.eot');
      src: url('./assets/fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
      url('./assets/fonts/ProximaNova-Regular.woff') format('woff'),
      url('./assets/fonts/ProximaNova-Regular.ttf') format('truetype');
      font-weight: normal;
      font-style: normal;
      }

      @font-face {
        font-family: 'Proxima Nova Extra Bold';
        src: url('./assets/fonts/ProximaNova-Extrabld.eot');
        src: url('./assets/fonts/ProximaNova-Extrabld.eot?#iefix') format('embedded-opentype'),
        url('./assets/fonts/ProximaNova-Extrabld.woff') format('woff'),
        url('./assets/fonts/ProximaNova-Extrabld.ttf') format('truetype');
        font-weight: normal;
        font-style: normal;
        }

        @font-face {
          font-family: 'Gilroy Extra Bold';
          src: url('./assets/fonts/Gilroy-ExtraBold.eot');
